import axios from 'src/utils/axios';
import authService from 'src/services/authService';
import { API_BASE_URL } from 'src/config';
import { USER } from 'src/config/endpoint';

export const LOGIN_REQUEST = '@account/login-request';
export const LOGIN_SUCCESS = '@account/login-success';
export const LOGIN_FAILURE = '@account/login-failure';
export const SILENT_LOGIN = '@account/silent-login';
export const LOGOUT = '@account/logout';
export const REGISTER = '@account/register';
export const UPDATE_PROFILE = '@account/update-profile';
export const UPDATE_PROFILE_PICTURE = '@account/update-profile-picture';

export function login(email, password) {
  return async (dispatch) => {
    try {
      dispatch({ type: LOGIN_REQUEST });

      const user = await authService.loginWithEmailAndPassword(email, password);
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          user
        }
      });
    } catch (error) {
      dispatch({ type: LOGIN_FAILURE });
      throw error;
    }
  };
}

export function setUserData(user) {
  return (dispatch) => dispatch({
    type: SILENT_LOGIN,
    payload: {
      user
    }
  });
}

export function logout() {
  return async (dispatch) => {
    authService.logout();

    dispatch({
      type: LOGOUT
    });
  };
}

export function register(user) {
  return async (dispatch) => {
    const {
      first_name, last_name, email, password
    } = user;

    axios.post(`${API_BASE_URL}/${USER.SIGN_UP}`, {
      first_name,
      last_name,
      email,
      password
    })
      .then((response) => {
        if (response.data.user) {
          authService.setSession(response.data.token);
          dispatch({
            type: LOGIN_SUCCESS,
            payload: {
              user: response.data.user
            }
          });
        }
      })
      .catch((error) => {
        throw error;
      });
  };
}

export function updateProfile(update) {
  const request = axios.put(`${API_BASE_URL}/${USER.EDIT_ME}`, update);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: UPDATE_PROFILE,
      payload: response.data
    }));
  };
}

export function updateProfilePicture(file) {
  const formData = new FormData();
  formData.append('file', file);
  const request = axios.post(`${API_BASE_URL}/${USER.PROFILE_PICTURE}`, formData);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: UPDATE_PROFILE_PICTURE,
      payload: response.data.profile_picture
    }));
  };
}
