/* eslint-disable no-param-reassign */
import produce from 'immer';
import _ from 'lodash';
import {
  GET_AVAILBLE_JOBS,
  GET_WORKERS,
  REMOVE_WORKERS,
  ASSIGN_JOB,
} from 'src/actions/workerActions';

const initialState = {
  availableJobs: [],
  workers: [],
};

const workerReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_AVAILBLE_JOBS: {
      const availableJobs = action.payload;

      return produce(state, (draft) => {
        // Ensure we clear current session
        draft.availableJobs = availableJobs;
      });
    }

    case ASSIGN_JOB: {
      const assignJob = action.payload;

      return produce(state, (draft) => {
        draft.availableJobs = _.remove(draft.availableJobs, (_job) => {
          if (_job._id !== assignJob) {
            return _job;
          }
        });
      });
    }

    case GET_WORKERS: {
      const workers = action.payload;
      return produce(state, (draft) => {
        draft.workers = workers;
      });
    }

    case REMOVE_WORKERS: {
      const selWorkers = action.payload;
      return produce(state, (draft) => {
        draft.workers = _.remove(draft.workers, (_worker) => {
          if (selWorkers.indexOf(_worker._id) === -1) {
            return _worker;
          }
        });
      });
    }

    default: {
      return state;
    }
  }
};

export default workerReducer;
