import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { SERVICE } from 'src/config/endpoint';

export const GET_SERVICE = '@service/get-service';
export const GET_SERVICES = '@service/get-services';
export const CREATE_SERVICE = '@service/create-service';
export const SET_SERVICE = '@service/set-service';
export const REMOVE_SERVICE = '@service/remove-service';
export const SET_HEADINGS = '@service/set-headings';
export const SET_DETAILS = '@service/set-details';
export const SET_CUSTOMER = '@service/set-customer';
export const SET_NEW_SERVICE = '@service/set-new-service';

export function createService(data) {
  const { file, service } = data;
  let request;

  if (file) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('service', JSON.stringify(service));
    request = axios
      .post(`${API_BASE_URL}/${SERVICE.CREATE}`, formData);
  } else {
    request = axios
      .post(`${API_BASE_URL}/${SERVICE.UPDATE}`, service);
  }

  return (dispatch) => {
    request.then(() => dispatch({
      type: CREATE_SERVICE,
    }));
  };
}

export function getService(serviceId) {
  const request = axios.get(`${API_BASE_URL}/${SERVICE.GET}/${serviceId}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_SERVICE,
      payload: response.data.service
    }));
  };
}

export function setService(data) {
  const { service, file, serviceId } = data;
  let request;
  if (file) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('service', JSON.stringify(service));
    request = axios
      .put(`${API_BASE_URL}/${SERVICE.UPDATE}/${serviceId}`, formData);
  } else {
    request = axios
      .put(`${API_BASE_URL}/${SERVICE.UPDATE}/${serviceId}`, service);
  }

  return (dispatch) => {
    request.then((response) => dispatch({
      type: SET_SERVICE,
      payload: response.data
    }));
  };
}

export function getServices() {
  const request = axios.get(`${API_BASE_URL}/${SERVICE.GET_ALL}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_SERVICES,
      payload: response.data.services
    }));
  };
}

export function removeService(service) {
  const request = axios.delete(`${API_BASE_URL}/${SERVICE.DELETE}/${service}`);

  return (dispatch) => {
    request.then(() => dispatch({
      type: REMOVE_SERVICE,
      payload: service
    }));
  };
}
