/* eslint-disable react/no-array-index-key */
import React, {
  lazy,
  Suspense,
  Fragment
} from 'react';
import {
  Switch,
  Redirect,
  Route
} from 'react-router-dom';
import DashboardLayout from 'src/layouts/DashboardLayout';
import MainLayout from 'src/layouts/MainLayout';
import HomeView from 'src/views/pages/HomeView';
import LoadingScreen from 'src/components/LoadingScreen';
import AuthGuard from 'src/components/AuthGuard';
import GuestGuard from 'src/components/GuestGuard';

const routesConfig = [
  {
    exact: true,
    path: '/',
    component: () => <Redirect to="/login" />
  },
  {
    exact: true,
    path: '/404',
    component: lazy(() => import('src/views/pages/Error404View'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/login',
    component: lazy(() => import('src/views/auth/LoginView'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/register',
    component: lazy(() => import('src/views/auth/RegisterView'))
  },
  {
    path: '/app',
    guard: AuthGuard,
    layout: DashboardLayout,
    routes: [
      {
        exact: true,
        path: '/app',
        component: () => <Redirect to="/app/dashboard" />
      },
      {
        exact: true,
        path: '/app/account',
        component: lazy(() => import('src/views/pages/AccountView'))
      },
      {
        exact: true,
        path: '/app/dashboard',
        component: lazy(() => import('src/views/reports/DashboardView'))
      },
      {
        exact: true,
        path: '/app/workers/:workerId/edit',
        component: lazy(() => import('src/views/management/WorkerEditView'))
      },
      {
        exact: true,
        path: '/app/workers/create',
        component: lazy(() => import('src/views/management/WorkerCreateView'))
      },
      {
        exact: true,
        path: '/app/workers/:workerId',
        component: lazy(() => import('src/views/management/WorkerDetailsView'))
      },
      {
        exact: true,
        path: '/app/workers',
        component: lazy(() => import('src/views/management/WorkerListView'))
      },
      {
        exact: true,
        path: '/app/services/:serviceId/edit',
        component: lazy(() => import('src/views/projects/OverviewView'))
      },
      {
        exact: true,
        path: '/app/services',
        component: lazy(() => import('src/views/projects/ServiceBrowseView'))
      },
      {
        exact: true,
        path: '/app/services/create',
        component: lazy(() => import('src/views/projects/ServiceCreateView'))
      },
      {
        exact: true,
        path: '/app/services/:serviceId',
        component: lazy(() => import('src/views/projects/ProjectDetailsView'))
      },
      {
        exact: true,
        path: '/app/jobs/create',
        component: lazy(() => import('src/views/management/JobCreateView/index.js'))
      },
      {
        exact: true,
        path: '/app/jobs',
        component: lazy(() => import('src/views/management/JobListView'))
      },
      {
        exact: true,
        path: '/app/jobs/:jobId/edit',
        component: lazy(() => import('src/views/management/JobDetailEditView'))
      },
      {
        exact: true,
        path: '/app/jobs/:jobId',
        component: lazy(() => import('src/views/management/JobDetailsView'))
      },
      {
        exact: true,
        path: '/app/customers',
        component: lazy(() => import('src/views/management/CustomerListView'))
      },
      {
        exact: true,
        path: '/app/customers/:customerId/edit',
        component: lazy(() => import('src/views/management/CustomerEditView'))
      },
      {
        exact: true,
        path: '/app/customers/create',
        component: lazy(() => import('src/views/management/CustomerCreateView'))
      },
      {
        exact: true,
        path: '/app/customers/:customerId',
        component: lazy(() => import('src/views/management/CustomerDetailsView'))
      },
      {
        exact: true,
        path: '/app/reports',
        component: lazy(() => import('src/views/projects/ReportListView'))
      },
      {
        exact: true,
        path: '/app/reports/:templateId/edit',
        component: lazy(() => import('src/views/projects/ReportEditView'))
      },
      {
        exact: true,
        path: '/app/reports/create',
        component: lazy(() => import('src/views/projects/ReportCreateView'))
      },
      {
        exact: true,
        path: '/app/reports/:templateId',
        component: lazy(() => import('src/views/management/CustomerDetailsView'))
      },
      {
        exact: true,
        path: '/app/management',
        component: lazy(() => import('src/views/kanban/KanbanView'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  },
  {
    path: '*',
    layout: MainLayout,
    routes: [
      {
        exact: true,
        path: '/home',
        component: HomeView
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  }
];

const renderRoutes = (routes) => (routes ? (
  <Suspense fallback={<LoadingScreen />}>
    <Switch>
      {routes.map((route, i) => {
        const Guard = route.guard || Fragment;
        const Layout = route.layout || Fragment;
        const Component = route.component;

        return (
          <Route
            key={i}
            path={route.path}
            exact={route.exact}
            render={(props) => (
              <Guard>
                <Layout>
                  {route.routes
                    ? renderRoutes(route.routes)
                    : <Component {...props} />}
                </Layout>
              </Guard>
            )}
          />
        );
      })}
    </Switch>
  </Suspense>
) : null);

function Routes() {
  return renderRoutes(routesConfig);
}

export default Routes;
