import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { JOB } from 'src/config/endpoint';

export const GET_JOB = '@job/get-job';
export const GET_JOBS = '@job/get-jobs';
export const SET_SERVICE = '@job/set-headings';
export const SET_DETAILS = '@job/set-details';
export const SET_CUSTOMER = '@job/set-customer';
export const CREATE_JOB = '@job/create-job';
export const REMOVE_JOB = '@job/remove-job';

export function getJobs() {
  const request = axios.get(`${API_BASE_URL}/${JOB.GET_ALL}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_JOBS,
      payload: response.data.jobs
    }));
  };
}

export function getJob(jobId) {
  const request = axios.get(`${API_BASE_URL}/${JOB.GET_ALL}/${jobId}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_JOB,
      payload: response.data.job,
    }));
  };
}

export function createJob(job) {
  const request = axios.post(`${API_BASE_URL}/${JOB.CREATE}`, job);

  return (dispatch) => {
    request.then(() => dispatch({
      type: CREATE_JOB,
    }));
  };
}

export function removeJob(job) {
  const request = axios.delete(`${API_BASE_URL}/${JOB.DELETE}/${job}`);

  return (dispatch) => {
    request.then(() => dispatch({
      type: REMOVE_JOB,
      payload: job
    }));
  };
}
