import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { REPORT } from 'src/config/endpoint';

export const GET_REPORTS = '@report/get-reports';
export const GET_REPORT = '@report/get-report';
export const CREATE_REPORT = '@report/create-report';

export function getReports() {
  const request = axios.get(`${API_BASE_URL}/${REPORT.GET_ALL}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_REPORTS,
      payload: response.data.report_templates,
    }));
  };
}

export function getReport(id) {
  const request = axios.get(`${API_BASE_URL}/${REPORT.GET}/${id}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_REPORT,
      payload: response.data.report,
    }));
  };
}

export function createReport(report) {
  const request = axios.post(`${API_BASE_URL}/${REPORT.CREATE}`, { report });

  return (dispatch) => {
    request.then(() => dispatch({
      type: CREATE_REPORT,
      payload: report,
    }));
  };
}
