import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { CUSTOMER } from 'src/config/endpoint';

export const GET_CUSTOMERS = '@customer/get-customers';
export const REMOVE_CUSTOMERS = '@customer/remove-customers';

export function getCustomers() {
  const request = axios.get(`${API_BASE_URL}/${CUSTOMER.GET_ALL}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_CUSTOMERS,
      payload: response.data.customers,
    }));
  };
}

export function removeCustomers(customers) {
  const request = axios.delete(`${API_BASE_URL}/${CUSTOMER.DELETE}`, {
    data: { customers }
  });

  return (dispatch) => {
    request.then(() => dispatch({
      type: REMOVE_CUSTOMERS,
      payload: customers
    }));
  };
}
