/* eslint-disable no-param-reassign */
import produce from 'immer';
import _ from 'lodash';
import {
  GET_JOBS,
  GET_JOB,
  SET_SERVICE,
  SET_DETAILS,
  SET_CUSTOMER,
  REMOVE_JOB,
} from 'src/actions/jobActions';

const initialState = {
  jobs: [],
  job: null,
};

const jobReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_JOBS: {
      const jobs = action.payload;
      return produce(state, (draft) => {
        draft.jobs = jobs;
      });
    }

    case GET_JOB: {
      const job = action.payload;
      return produce(state, (draft) => {
        draft.job = job;
      });
    }

    case SET_DETAILS: {
      const {
        title, description, due_start, due_end
      } = action.payload;
      return produce(state, (draft) => {
        draft.job = {
          ...state.job, title, description, due_start, due_end
        };
      });
    }

    case SET_CUSTOMER: {
      const customer = action.payload;
      return produce(state, (draft) => {
        draft.job = { ...state.job, customer };
      });
    }

    case SET_SERVICE: {
      const service = action.payload;
      return produce(state, (draft) => {
        draft.job = { ...state.job, service };
      });
    }

    case REMOVE_JOB: {
      const job = action.payload;
      return produce(state, (draft) => {
        draft.jobs = _.remove(draft.jobs, (_job) => {
          if (_job._id !== job) {
            return _job;
          }
        });
      });
    }

    default: {
      return state;
    }
  }
};

export default jobReducer;
