import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { JOB, WORKER } from 'src/config/endpoint';

export const GET_AVAILBLE_JOBS = '@worker/get-available';
export const GET_WORKERS = '@worker/get-workers';
export const REMOVE_WORKERS = '@worker/remove-workers';
export const ASSIGN_JOB = '@worker/assign-job';

export function getAvailableJobs(customer) {
  const request = axios.get(`${API_BASE_URL}/${JOB.GET_AVAILABLE}/${customer}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_AVAILBLE_JOBS,
      payload: response.data.jobs
    }));
  };
}

export function getWorkers() {
  const request = axios.get(`${API_BASE_URL}/${WORKER.GET_ALL}`);

  return (dispatch) => {
    request.then((response) => dispatch({
      type: GET_WORKERS,
      payload: response.data.workers
    }));
  };
}

export function removeWorkers(workers) {
  const request = axios.post(`${API_BASE_URL}/${WORKER.BULK_DELETE}`, { workers });

  return (dispatch) => {
    request.then(() => dispatch({
      type: REMOVE_WORKERS,
      payload: workers
    }));
  };
}

export function assignJob(job, worker_id) {
  const request = axios.post(`${API_BASE_URL}/${WORKER.ASSIGN_JOB}`, { job, worker_id });

  return (dispatch) => {
    request.then(() => dispatch({
      type: ASSIGN_JOB,
      payload: job,
    }));
  };
}
