/* eslint-disable no-param-reassign */
import produce from 'immer';
import _ from 'lodash';
import {
  SET_SERVICE,
  GET_SERVICES,
  GET_SERVICE,
  SET_HEADINGS,
  SET_DETAILS,
  REMOVE_SERVICE,
  SET_NEW_SERVICE,
  SET_CUSTOMER,
} from 'src/actions/serviceActions';

const initialState = {
  services: [],
  service: null,
  newService: null,
  addingTodo: false,
  headings: [],
  file: null,
};

const serviceReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_SERVICE: {
      const services = action.payload;
      return produce(state, (draft) => {
        draft.service = services;
      });
    }

    case GET_SERVICES: {
      const services = action.payload;
      return produce(state, (draft) => {
        draft.services = services;
      });
    }

    case SET_SERVICE: {
      const service = action.payload;
      return produce(state, (draft) => {
        // Ensure we clear current session
        draft.service = { ...state.service, ...service };
      });
    }

    case REMOVE_SERVICE: {
      const service = action.payload;
      return produce(state, (draft) => {
        draft.services = _.remove(draft.services, (_service) => {
          if (_service._id !== service) {
            return _service;
          }
        });
      });
    }

    case SET_NEW_SERVICE: {
      const newService = action.payload;
      return produce(state, (draft) => {
        draft.newService = newService;
      });
    }

    case SET_HEADINGS: {
      const headings = action.payload;
      return produce(state, (draft) => {
        draft.newService = { ...state.newService, headings };
      });
    }

    case SET_DETAILS: {
      const { title, description, file } = action.payload;
      return produce(state, (draft) => {
        draft.newService = { ...state.newService, title, description };
        draft.file = file;
      });
    }

    case SET_CUSTOMER: {
      const customer = action.payload;
      return produce(state, (draft) => {
        draft.newService = { ...state.newService, customer };
      });
    }


    default: {
      return state;
    }
  }
};

export default serviceReducer;
